@import url('https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:wght@300;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap');

* {
  padding: 0;
  margin: 0;
  font-family: 'Plus Jakarta Sans', sans-serif;
  font-weight: 300;
}

body{
  position: relative;
  background-color: rgb(235, 234, 232);
}

button {
  cursor: pointer;
}

@font-face {
  font-family: 'icomoon';
  src:  url('./icomoon/fonts/icomoon.eot');
  src:  url('./icomoon/fonts/icomoon.eot') format('embedded-opentype'),
    url('./icomoon/fonts/icomoon.ttf') format('truetype'),
    url('./icomoon/fonts/icomoon.woff') format('woff'),
    url('./icomoon/fonts/icomoon.svg') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-add-sign:before {
  content: "\e900";
}

.icon-cancel-circle:before {
  content: "\ea0d";
}
