@import url(https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:wght@300;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
* {
  padding: 0;
  margin: 0;
  font-family: 'Plus Jakarta Sans', sans-serif;
  font-weight: 300;
}

body{
  position: relative;
  background-color: rgb(235, 234, 232);
}

button {
  cursor: pointer;
}

@font-face {
  font-family: 'icomoon';
  src:  url(/static/media/icomoon.acbdff32.eot);
  src:  url(/static/media/icomoon.acbdff32.eot) format('embedded-opentype'),
    url(/static/media/icomoon.369f87c0.ttf) format('truetype'),
    url(/static/media/icomoon.68772a3e.woff) format('woff'),
    url(/static/media/icomoon.ec1eb8fd.svg) format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-add-sign:before {
  content: "\E900";
}

.icon-cancel-circle:before {
  content: "\EA0D";
}

